h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  margin: 1.875rem 0 1.125rem 0;
  line-height: 1.35;
  font-weight: 700;
  font-family: $font2;
  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 0;
  }
  & + h2, .h2,
  & + h3, .h3,
  & + h4, .h4,
  & + h5, .h5,
  & + h6, .h6 {
    margin-top: 2.375rem;
  }
}

p {
  margin: 0 0 1rem 0;
  &:last-child {
    margin-bottom: 0;
  }
}

strong, b {
  font-weight: 700;
}

a {
  text-decoration: none;
  color: inherit;
  &:not([class]), &.link {
    transition: opacity $trs1, border-color $trs1, color $trs1;
    border-bottom: 1px solid currentColor;
    &[data-touch], &[data-hover] {
      border-color: transparent;
      color: var(--color-text);
    }
    &[data-focus] {
      opacity: 0.8;
    }
  }
}

h1, .h1 {
  font-size: 3.25rem;
}
h2, .h2 {
  font-size: 2.875rem;
}
h3, .h3 {
  font-size: 2.25rem;
}
h4, .h4 {
  font-size: 1.375rem;
  font-family: $font1;
}
h5, .h5 {
  font-size: 1.25rem;
  font-weight: 500;
  font-family: $font1;
}
h6, .h6 {
  font-size: 1.25rem;
  font-family: $font1;
}


@include media-breakpoint-down(xxl) {
  h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
    margin: 1.5rem 0 0.875rem 0;
  }
  h1, .h1 {
    font-size: 2.625rem;
  }
  h2, .h2 {
    font-size: 2.5rem;
  }
  h3, .h3 {
    font-size: 1.625rem;
  }
  h4, .h4 {
    font-size: 1.25rem;
  }
  h5, .h5 {
    font-size: 1.125rem;
  }
  h6, .h6 {
    font-size: 1rem;
  }
}

@include media-breakpoint-down(sm) {
  h1, .h1 {
    font-size: 1.5rem;
  }
  h2, .h2 {
    font-size: 1.375rem;
  }
  h3, .h3 {
    font-size: 1rem;
  }
  h4, .h4 {
    font-size: 1rem;
  }
  h5, .h5 {
    font-size: 0.875rem;
  }
  h6, .h6 {
    font-size: 0.875rem;
  }
}